import { ChartData } from './interface';
import { Drawer } from '../drawer';
import { Detailed } from './detailed';
import { Slider } from './slider';

export class Chart {
  private detailed: Detailed;
  private slider: Slider;

  constructor(chartId: string, data: ChartData) {
    const root = document.getElementById(chartId);

    if (!root) {
      throw new Error('DIV with id="chart" is not found');
    }

    root.innerHTML = this.getTemplate();

    const ctxDetailed = <CanvasRenderingContext2D>(
      (<HTMLCanvasElement>document.getElementById('detailed')).getContext('2d')
    );
    const ctxSlider = <CanvasRenderingContext2D>(
      (<HTMLCanvasElement>document.getElementById('slider')).getContext('2d')
    );

    this.update = this.update.bind(this);

    this.detailed = new Detailed(new Drawer(ctxDetailed), data);
    this.slider = new Slider(new Drawer(ctxSlider), data, this.update);
  }

  private getTemplate() {
    return `
      <canvas id="detailed" width="768" height="432"></canvas>
      <canvas id="slider" width="768" height="48"></canvas>
        <div id="range" data-type-shift="shift">
          <img data-type-shift="left" src="data:image/svg+xml;base64,"/>
          <img data-type-shift="right" src="data:image/svg+xml;base64,"/>
        </div>
      `;
  }

  private update(leftIndex: number, rightIndex: number) {
    this.detailed.update(leftIndex, rightIndex);
  }

  render() {
    this.detailed.render();
    this.slider.render();
  }
}
